<template>
    <div class="SearchWrap">
        <header>
            <router-link to="/food">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <div><span>搜索</span></div>
        </header>
        <div class="search_form">
            <input type="text" placeholder="请输入商家或美食名称" class="search_input" v-model="inputV">
            <button class="search_submit" @click="searchstore">提交</button>
        </div>
        <div class="search_none" :class="{search_none1:isshow}">很抱歉！无搜索结果</div>
        <section class="foot_guide">
            <section class="guide_item">
                <i class="el-icon-eleme"></i>
                <span>外卖</span>
            </section>
            <section class="guide_item">
                <i class="el-icon-zoom-out"></i>
                <span>搜索</span>
            </section>
            <section class="guide_item">
                <i class="el-icon-shopping-cart-2"></i>
                <span>订单</span>
            </section>
            <section class="guide_item">
                <i class="el-icon-user"></i>
                <span>我的</span>
            </section>
        </section>
    </div>
</template>

<script>
    export default {
        name: "Search",
        data(){
            return{
                geoHash:0,
                inputV:"",
                storeArr:[],
                isshow:true
            }
        },
        created(){
            this.geoHash = this.$route.query.geoHash;
            console.log(this.geoHash);
        },
        methods:{
            searchstore(){
                if(this.inputV == ""){
                    alert("请输入内容!");
                }else{
                    this.isshow = !this.isshow;
                    this.axios.get("/v4/restaurants", {
                        params:{
                            geohash :this.geoHash,
                            keyword :this.inputV
                        }
                    }).then(res=>{
                        this.storeArr = res;
                        console.log(this.storeArr);
                    });
                }


            }
        }
    }
</script>

